import React, {useEffect, useState, useRef, useCallback} from "react";
import '../css/game.css';
import gameSelect from '../data/game.json';
import { Fireworks } from 'fireworks-js/dist/react' 
//result > connect right now 루트 처리하기 ★★★★☆☆☆☆☆☆☆
//result > retry시 버튼 active 초기화

export default function Game(){
    //firework  ????? https://fireworks.js.org/
    const options = {
        speed:6,
        particle : 307,
      }
    
      const style = {
        left: 0,
        top: 0,
        width: '1000px',
        height: '78vh',
        position: 'fixed',
        background: '#000',
        top:'50%',
        left:'50%',
        zIndex:-1,
        transform:'translate(-50%,-50%)'
      }
    


    //핑크색 파란색 박스 처리 & result에 넣을 단어 배열로만들기
    // https://www.youtube.com/watch?v=-Lx-YlI9hlY
    const [type, typeSetstate] = useState([])
    const activeBlue = (e) => {
        if( e.target.className === "blue"){
            e.target.className = "blue active"
            typeSetstate([...type, e.target.dataset.work])
        }else{
            e.target.className = "blue"
            typeSetstate(type.filter( work => work !== e.target.dataset.work))
        }
    }

    const activePink = (e) => {
        if( e.target.className === "pink"){
            e.target.className = "pink active"
            typeSetstate([...type, e.target.dataset.work])
        }else{
            e.target.className = "pink"
            typeSetstate(type.filter( work => work !== e.target.dataset.work))
        }
    }

    // submit 버튼 실행
    const [submit, submitSetstate] = useState("isntActive");
    const submitOn = () => { submitSetstate("isActive"); } //result창 생김
    const submitOff = () => { submitSetstate("isntActive"); } //result창 생김
    const [fire, setFire] = useState(false);

    const resultClass = (submit) => {
        if(submit == "isActive"){
            setTimeout(setFire,100,true);
            return( "result active")
        }else{
            return( "result")
        }
    }

    return(
        <div className="game_div">
            <p className="game_title">Find Your Employee</p>
            <p className="game_sub">Select personalities you want your employee to have <br></br> 여러분이 원하는 직장동료 유형을 골라주세요.</p>
            <div className="select">
                {
                    gameSelect.all.map((v,k) =>
                        {
                            return (
                                <div className={`sel_wrap ${v.section}`} key={k}>
                                    <div 
                                        className="pink" 
                                        onClick={activePink} 
                                        data-work={v.pink_work}
                                     >
                                         {v.pinkKr}<br></br>{v.pinkEn}
                                    </div>
                                    <div 
                                        className="blue"
                                        onClick={activeBlue} 
                                        data-work={v.blue_work}
                                    >
                                        {v.blueKr}<br></br>{v.blueEn}
                                    </div>
                                </div>
                            )
                        }
                    )
                 }                
            </div>
            <div className="gam_submit" onClick={submitOn}>SUBMIT</div>

            <div className={resultClass(submit)}>
                {fire && <Fireworks options={options} style={style}/>}
                <div className="result_wrap">
                    <div className="result_img"></div>
                    <div className="employ"> 
                        <p>당신에게 필요한 인재는</p>
                        {
                            type.map((v,k) => {
                                    return <p>{v}</p>
                                }
                            )
                        }
                        <p className="name">빛나</p>
                        <p >입니다</p>
                    </div>
                    <div className="btn">  
                        <div className="retry" onClick={submitOff}>Retry</div>
                        <div 
                            className="connect"
                            onClick={()=>{window.open('https://open.kakao.com/o/s0P3Y6Ed', '_blank')}}
                        >
                            Connect Right Now
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}