import React, {useEffect, useState, useRef, useCallback} from "react";
import '../css/contact.css';
import Confetti from 'react-confetti'


// css animate  https://animate.style/
//????? 클릭하면 emoji or heart 나오게  참고 https://codepen.io/ppmathis/pen/uBkIn https://codepen.io/rachsmith/pen/BKjvqK
// ??????? form 전송하는방법


export default function Contact(){
    //이미지 hover 이벤트
    const [num, setNone] = useState(0)

    window.onload = ()=>{

    }//onload end

    return(
        <div className="cont_div">
                <Confetti
                    className={num==0 ? "none"  : ""}
                />
            <div className="cont_wrap">
                <div className="cont_left">
                    <p className="left_1st">Get in Touch</p>
                    <p className="left_2nd">Thank you for taking your time</p>
                    <div className="left_img">
                        <img 
                            src="./img/contact/thankyou.jpg"
                            onMouseOver={()=>{setNone(1)}}
                            onMouseOut={()=>{setNone(0)}}
                        >
                        </img>
                    </div>
                </div>
                <div className="cont_right">
                    <p className="cont_name">
                        <label>Name</label>
                        <input type="text" id="name" name="name"></input>
                    </p>
                    <p className="cont_email">
                        <label>Email</label>
                        <input type="text" id="email" name="email"></input>
                    </p>
                    <p className="cont_sub">
                        <label>Subject</label>
                        <input type="text" id="sub" name="sub"></input>
                    </p>
                    <p className="cont_msg">
                        <label>Message</label>
                        <textarea id="msg" name="msg"></textarea>
                    </p>
                    <button className="cont_btn" type="submit">Submit</button>
                </div>
            </div>
        </div>
    )
}