import React from "react";
import { VectorMap } from "react-jvectormap";


const mapData = {
    CN: 100000,
    JP: 100000,
    CN: 100000,
    TW: 100000,
    KR: 100000,
    SG: 100000,
    MY: 100000,
    VN: 100000,
    TH: 100000,
    BE: 100000,
    FR: 100000,
    DE: 100000,
    NL: 100000,
    ES: 100000,
    AU: 100000,
  };
  
  const handleClick = (e, countryCode) => {
    console.log(countryCode);
  };
  
export  default function TravelMap () {
    return (
      <div>
        <VectorMap
          map={"world_mill"}
          backgroundColor="transparent" //change it to ocean blue: #0077be
          zoomOnScroll={false}
          zoomAnimate={false}
          zoomStep='0'
          containerStyle={{
            width: "100%",
            height: "520px"
          }}
          onRegionClick={handleClick} //gets the country code
          containerClassName="map"
          regionStyle={{
            initial: {
              fill: "#d3d3d3",
              "fill-opacity": 0.9,
              stroke: "none",
              "stroke-width": 0,
              "stroke-opacity": 0
            },
            hover: {
              "fill-opacity": 0.8,
              cursor: "pointer"
            },
            selected: {
              fill: "#2938bc" //color for the clicked country
            },
            selectedHover: {}
          }}
          regionsSelectable={true}
          series={{
            regions: [
              {
                values: mapData, //this is your data
                scale: ["#146804", "#3366ff"], //your color game's here
                normalizeFunction: "polynomial"
              }
            ]
          }}
        />
      </div>
    );
  };