import React, {useEffect, useState, useRef, useCallback} from "react";
import  '../css/detail.css';
import { Link, Route, BrowserRouter as Router} from 'react-router-dom';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Details from '../data/details.json';


export default function Detail({a}) {

    //detail json 불러오기
    let param = Details[a];
    // console.log(Details[a]);
    // console.log(param[1].phoneDisplay);

//★★★★★★★★★★★★★★★★★★★★★★★★★★★★★
//스크롤이벤트 이페이지에서만 먹게하기. 다른 팝업창뜰때 거기에서먹히게
    // scrolltrigger https://blog.logrocket.com/how-to-use-the-gsap-scrolltrigger-plugin-in-react/
    gsap.registerPlugin(ScrollTrigger);
    const ref = useRef(null);
    useEffect( () => {
        const element = ref.current;
        const card = document.querySelectorAll(`.features_wrap`);
        for(let i=0; i<card.length; i++){
            let div = `.feat${i+1}`;
            let point = i*70;
            gsap.to(
                element.querySelector(div),{
                    rotateY:180,
                    scrollTrigger:{
                        trigger : element.querySelector('.detail_div'),
                        start : `${point +10 } top`,
                        end : `${point + 30} top`,
                        scrub: true,
                        markers:false
                    }
                }
            );
        }
 
    },[] );




// monitor 476 * 270
// phone 134 * 239

    return(
        <div className="detail_div" ref={ref} >


            <div className={`${param[0].classname} detail_wrap`} >
                <div className="detail_title">
                    <div className="title_img">
                        <div className="monitor" >
                            <div 
                                className="monitor_img"
                                style={{background: param[1].monitor}}
                            ></div>
                        </div>
                        <div className="phone" style={{display:param[1].phoneDisplay}}>
                            <div 
                                className="phone_img"
                                style={{background: param[1].phone}}
                            ></div>
                        </div>
                    </div>
                    <div className="title_sub">
                        <p>{param[1].name}</p>
                        <span
                        onClick={()=>{window.open(param[1].link, '_blank')}}
                        >
                            Go to {param[1].name} !
                        </span>
                    </div>
                </div>
                <div className="detail_intro">
                    <p>{param[1].intro1}</p>
                    <p>{param[1].intro2}</p>
                </div>

                <div className="detail_sub">
                    {/* 1200*450 front:맨처음 보이는 그곳 back:이 내용물 */}
                    {param[2].detail.map((detail,k) => {
                            return(
                                <div className={`${detail.class} features_wrap`} key={k}  style={{height:detail.height1}}>
                                    <div className="feat_front">
                                        <p>{detail.back_en}</p>
                                        <p>{detail.back_ko}</p>
                                    </div>
                                    <div className="feat_back">
                                        <div 
                                            className="back_left" 
                                            style={{
                                                width:detail.width1,
                                                height:detail.height1,
                                                background: detail.bg1
                                        }}>
                                        </div>
                                        <div 
                                            className="back_mid" 
                                            style={{
                                                display:detail.display,
                                                width:detail.width2,
                                                height:detail.height2,
                                                background:detail.bg2
                                        }}>
                                        </div>
                                        <div 
                                            className="back_right"
                                            style={{}}>
                                            <p>{detail.ex_en}</p>
                                            <p>{detail.ex_ko}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        }
                    )}
                </div>

                
            </div>

        </div>
    
    
    );


}


