import React from "react";
import {useEffect, useState, useRef} from "react";
import { Link, Route, BrowserRouter as Router } from "react-router-dom";
import MaterialIcon from 'react-google-material-icons';
import Main from './component/main';
import Works from './component/works';
import ItsMe from './component/itsMe';
import Game from './component/game';
import Contact from './component/contact';
import Detail from './component/detail';
import './App.css';
import githubImg from './img/github_icon.png';
import katalkImg from './img/kakaotalk_icon.png';
import logo from './img/logo.png';

//다른 페이지에서 메뉴를 너비에 포함시키지 않느다.

export default function App() {

  //mousehover 시 메뉴 이름 보이게
  const [hover, setHover] = useState(false)
  const menuName = (hover) => {
    return(hover === true ? {display:'block'} : {display:'none'})
  }

  //mouseHover 메일 보이기
  const [mail, setMail] = useState(false);
  const showMail = (mail) => {
    return(mail === true ? {display:'block'} : {display:'none'})
  }

  return (
    <div className="menu" >
      <Router basename="/portfolio">
        <header className="header">
          <div className="header_div">
            <h2>
              <Link to="/">
                <img className="logo" src={logo} alt="logo"></img>
              </Link>
            </h2>
            <nav className="nav" onMouseEnter={()=>{setHover(true)}} onMouseLeave={()=>{setHover(false)}}>
              <div className="mainMenu">
                <Link to="/" >
                  <MaterialIcon icon="home" size={43} style={{color:'white'}}/>
                </Link>
                <p className="menuName" style={menuName(hover)}>Home</p>
              </div>
              <div className="mainMenu">
                <Link to="/Works">
                  <MaterialIcon icon="description" size={43} style={{color:'white'}}/>
                </Link>  
                <p className="menuName" style={menuName(hover)}>Works</p>
              </div>
              <div className="mainMenu" >
                <Link to="/ItsMe">
                  <MaterialIcon icon="person" size={43} style={{color:'white'}}/>
                </Link> 
                <p className="menuName" style={menuName(hover)}>It's Me</p>
              </div>
              <div className="mainMenu">
                <Link to="/Game">
                  <MaterialIcon icon="sports_esports" size={43} style={{color:'white'}}/>
                </Link>   
                <p className="menuName" style={menuName(hover)}>Game</p>             
              </div>
              <div className="mainMenu">
                <Link to="/Contact">
                  <MaterialIcon icon="phone_in_talk" size={43} style={{color:'white'}}/>
                </Link>
                <p className="menuName" style={menuName(hover)}>Contact</p>                
              </div>




            </nav>
            <div className="side">
              <p>
                  <img 
                    src={githubImg} 
                    alt="github" 
                    onClick={()=>{window.open('https://github.com/bitnana', '_blank')} }
                  />
              </p>
              <p>
                <img 
                  src={katalkImg} 
                  alt="kakaotalk"
                  onClick={()=>{window.open('https://open.kakao.com/o/s0P3Y6Ed', '_blank')}}
                />
              </p>
              <p onMouseEnter={()=>{setMail(true)}} onMouseLeave={()=>{setMail(false)}}>
                <MaterialIcon icon="email" size={32} style={{color:'white'}}/>
                <p style={showMail(mail)} className="mail">zpdlzm326@naver.com</p>
              </p>

            </div>
          </div>
        </header>
        <main>
          <Route exact path="/" component={Main} />
          <Route path="/Works" component={Works} />
          <Route path="/ItsMe" component={ItsMe} />
          <Route path="/Game" component={Game} />
          <Route path="/Contact" component={Contact} />
          <Route path="/Detail" component={Detail} />
        </main>
      </Router>
    </div>
  );
}

